import React from "react";
import BookingBar from "../BookingBar/BookingBar";
import BookingFinalCheck from "../BookingFinalCheck/BookingFinalCheck";

import * as S from "./BookingCheck.styles";
import { IconCalender, IconClock, IconUsers } from "../../assets/index";
import { useState } from "react";

const BookingCheck = ({ setIsBookingOpen }) => {
  const [isNext, setIsNext] = useState(false);

  const handleRightBtnClick = () => {
    setIsNext(true);
    console.log(isNext);
  };

  return !isNext ? (
    <BookingBar
      leftBtn={""}
      rightBtn={""}
      setIsBookingOpen={setIsBookingOpen}
      rightBtnOnClick={() => handleRightBtnClick()}
      leftBtnOnClick={() => setIsBookingOpen(false)}
    >
      <S.BookingCheckHeading>  ?</S.BookingCheckHeading>
      <div style={{ background: "#D9D9D9", height: "3px" }}></div>
      <S.BookingCheckSubheading>
              .
      </S.BookingCheckSubheading>
      <S.BookingCheckName></S.BookingCheckName>
      <S.BookingCheckLabel>   </S.BookingCheckLabel>
      <S.BookingCheckContainer>
        <S.Icon>
          <IconCalender></IconCalender>
          <S.IconLabel>1.06()</S.IconLabel>
        </S.Icon>
        <S.Icon>
          <IconClock></IconClock>
          <S.IconLabel> 6:30</S.IconLabel>
        </S.Icon>
        <S.Icon style={{ marginRight: "0px" }}>
          <IconUsers></IconUsers>
          <S.IconLabel>2</S.IconLabel>
        </S.Icon>
      </S.BookingCheckContainer>
      <S.BookingCheckWarning>
                  
            :)
      </S.BookingCheckWarning>
    </BookingBar>
  ) : (
    <BookingFinalCheck setIsBookingOpen={setIsBookingOpen}></BookingFinalCheck>
  );
};

export default BookingCheck;
