import React from "react";
import BookingBar from "../BookingBar/BookingBar";

import * as O from "../BookingCheck/BookingCheck.styles";
import * as S from "./BookingFinalCheck.styles";

import {
  IconCalender,
  IconClock,
  IconUsers,
  IconUncheck,
} from "../../assets/index";
import { useState } from 'react';
import BookingFinish from '../BookingFinish/BookingFinish'

const BookingFinalCheck = ({ setIsBookingOpen }) => {

  const [isNext, setIsNext] = useState(false);

  const handleRightBtnClick = () => {
    setIsNext(true);
    console.log(isNext)
  }

  return (
    !isNext ? (
      <BookingBar
      leftBtn={""}
      rightBtn={" "}
      setIsBookingOpen={setIsBookingOpen}
      rightBtnOnClick={() => handleRightBtnClick()}
      leftBtnOnClick={() => setIsBookingOpen(false)}
    >
        <O.BookingCheckHeading>  ?</O.BookingCheckHeading>
        <div style={{background: '#D9D9D9', height: '3px',}}></div>
        <O.BookingCheckName></O.BookingCheckName>
        <O.BookingCheckLabel>   </O.BookingCheckLabel>
        <O.BookingCheckContainer>
            <O.Icon>
                <IconCalender></IconCalender>
                <O.IconLabel>1.06()</O.IconLabel>
            </O.Icon>
            <O.Icon>
                <IconClock></IconClock>
                <O.IconLabel> 6:30</O.IconLabel>
            </O.Icon>
            <O.Icon style={{'marginRight': '0px'}}>
                <IconUsers></IconUsers>
                <O.IconLabel>2</O.IconLabel>
            </O.Icon>
        </O.BookingCheckContainer>
        <O.BookingCheckWarning>
                  ,
               .
        </O.BookingCheckWarning>
        <S.BookingCheckSublabel> ?</S.BookingCheckSublabel>
        <S.BookingCheckInput>
            <IconUncheck></IconUncheck>
            <S.BookingCheckInputLabel>  </S.BookingCheckInputLabel>
        </S.BookingCheckInput>
    </BookingBar>
    ) : (<BookingFinish setIsBookingOpen={setIsBookingOpen}></BookingFinish>)
    
  )
}

export default BookingFinalCheck;
