import React from 'react'
import BookingBar from '../BookingBar/BookingBar'

import * as O from "../BookingCheck/BookingCheck.styles"
import * as S from "./BookingFinish.styles"
import { useNavigate } from 'react-router-dom'

const BookingFinish = ({ setIsBookingOpen }) => {
  const navigate = useNavigate();

  return (
    <BookingBar leftBtn={" "} rightBtn={" "}
      setIsBookingOpen={setIsBookingOpen} 
      leftBtnOnClick={() =>  navigate('/')}
      >
        <O.BookingCheckHeading> .</O.BookingCheckHeading>
        <S.BookingStoreContainer>
            <S.BookingStoreLabel> </S.BookingStoreLabel>
            <S.BookingStoreName></S.BookingStoreName>
        </S.BookingStoreContainer>
        <S.BookingDateContainer>
            <S.BookingDateLabel></S.BookingDateLabel>
            <S.BookingDate>11.11 ()  6:30 2</S.BookingDate>
        </S.BookingDateContainer>
    </BookingBar>
  )
}

export default BookingFinish