import React from "react";
import * as S from "./MenuBox.styles";

const MenuBox = ({ menus }) => {
  return (
    <>
      {menus.map((menu, index) => (
        <S.Menu key={index}>
          <p className="name">{menu.name}</p>
          <p className="price">{menu.price.toLocaleString()}</p>
        </S.Menu>
      ))}
      <S.Description>
        *        .
      </S.Description>
    </>
  );
};

export default MenuBox;
